.flex_container {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.card_specif {
  width: 400px;
  background-color: #1a1e33;
  margin: 10px;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 1px -1px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 1px -1px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 1px -1px 5px 0px rgba(0, 0, 0, 0.75);
  border: #48aed7 solid 1px;
}

.card_title {
  font-family: "Fredoka One", cursive;
  font-size: 24px;
  color: #48aed7;
  text-align: center;
}

.link_card {
  border: #48aed7 solid 1px;
  padding: 10px;
  background-color: #2e3557;
  color: #fff;
  text-align: center;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}

.link_card:hover {
  background-color: #474f77;
}

.para {
  color: #fff;
  text-align: center;
}

button:focus {
  outline: none;
}

.icon_card {
  color: #fff;
  text-align: center;
  font-size: 50px;
}
