.base_image {
  width: 350px;
  border-radius: 10px;
}

.flex {
  display: flex;
  margin: 30px;
}

.flex_image {
  width: 250px;
  border-radius: 10px;
  margin: 10px 30px;
}

.rot {
  width: 140px;
  border-radius: 10px;
  margin: 10px 30px;
}

.function {
  color: #48aed7;
}

.buttonFunction {
  color: #48aed7;
  background-color: #111840;
  text-decoration: none;
  outline: none;
  padding: 12px;
  border: #48aed7 2px solid;
  border-radius: 5px;
}

.buttonFunction:hover {
  cursor: pointer;
}

.blue {
  background-color: #48aed7;
  height: 1px;
  border-radius: 10px;
  color: #48aed7;
  width: 90%;
}

.imgborder {
  border-color: black;
  border-style: solid;
}

.dashBorderImage {
  border: 2px black dashed;
}
